.table-row-headers,
.table-row-headers-1,
.table-row-headers-2,
.table-row-headers-3,
.table-row-headers-4,
.table-row-headers-5 {
  align-items: flex-start;
  align-self: stretch;
  background-color: var(--white);
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.inputs,
.inputs-1,
.inputs-2,
.inputs-3,
.inputs-4,
.inputs-5 {
  align-items: center;
  align-self: stretch;
  display: flex;
  gap: 10px;
  padding: 7px 10px;
  position: relative;
}

.checkbox,
.checkbox-1,
.checkbox-2,
.checkbox-3,
.checkbox-4,
.checkbox-5 {
  height: 20px;
  min-width: 20px;
  position: relative;
}

.row-data,
.row-data-1,
.row-data-2,
.row-data-3,
.row-data-4,
.row-data-5 {
  align-items: center;
  display: flex;
  flex: 1;
  gap: 10px;
  position: relative;
}

.column-header-4,
.column-header-4-1,
.column-header-4-2,
.column-header-4-3,
.column-header-4-4,
.column-header-4-5 {
  align-items: center;
  display: flex;
  flex: 1;
  gap: 10px;
  padding: 0px 10px;
  position: relative;
}

.text,
.text-1,
.text-2,
.text-3,
.text-4,
.text-5 {
  align-items: center;
  display: flex;
  flex: 1;
  gap: 10px;
  padding: 5px 0px;
  position: relative;
}

.label-31,
.label-32,
.label-33,
.label-34,
.label-35,
.label-36 {
  flex: 1;
  letter-spacing: 0;
  line-height: 14.5px;
  margin-top: -1.00px;
  position: relative;
  white-space: nowrap;
}

